import React, { useState, useEffect } from 'react';
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';
import articleContent from './article-content';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentsForm from '../components/AddCommentForm';

import FullStackDevBlog from '../content/FullStackDevBlog';
import PythonClassesBlog from '../content/PythonClassesBlog';
import PythonDecoratorsBlog from '../content/PythonDecoratorsBlog';
import PythonDependenciesManagementBlog from '../content/PythonDependenciesManagementBlog';
import PythonDundersBlog from '../content/PythonDundersBlog';
import PythonFirstOrderFunctionsBlog from '../content/PythonFirstOrderFunctions';
import PythonFunctionsBlog from '../content/PythonFunctionsBlog';
import PythonGeneratorsBlog from '../content/PythonGeneratorsBlog';
import PythonNamedTupleBlog from '../content/PythonNamedTupleBlog';


const getArticle = articleName => {
    let CustomComponent;
    switch (articleName) {
        case 'full-stack-development':
            CustomComponent = FullStackDevBlog;
            break;
        case 'python-classes':
            CustomComponent = PythonClassesBlog;
            break;
        case 'python-decorators':
            CustomComponent = PythonDecoratorsBlog;
            break;
        case 'python-dependencies-management':
            CustomComponent = PythonDependenciesManagementBlog;
            break;
        case 'python-dunders':
            CustomComponent = PythonDundersBlog;
            break;
        case 'python-first-order-functions':
            CustomComponent = PythonFirstOrderFunctionsBlog;
            break;
        case 'python-functions':
            CustomComponent = PythonFunctionsBlog;
            break;
        case 'python-generators':
            CustomComponent = PythonGeneratorsBlog;
            break;
        case 'python-named-tuple':
            CustomComponent = PythonNamedTupleBlog;
            break;
        default:
            CustomComponent = '';
            break;
    }
    return CustomComponent;
}

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);
    const CustomComponent = getArticle(name);
    
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();

            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!article) return <NotFoundPage />

    const otherArticles = articleContent.filter(article => article.name !== name);

    return (
        <>
        <h1>{article.title}</h1>
        <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
        <CustomComponent />    
        <CommentsList comments={articleInfo.comments} />
        <AddCommentsForm articleName={name} setArticleInfo={setArticleInfo} />
        <h3>Other Articles:</h3>
        <ArticlesList articles={otherArticles} />
        </>
    );
}

export default ArticlePage;