import * as React from 'react';
import Highlight from 'react-highlight'
import './dracula.css';

export class CodeSnippet extends React.Component {
    render() {
        return(
            <div data-test="code-snippet-div">
                <Highlight language={this.props.language}>
                     {this.props.code}
                </Highlight>
            </div>);
    }
}